export default {
	data() {
		return {
			title: 'Hello'
		}
	},
	onLoad() {
		const token=uni.getStorageSync('token');
		 const user = uni.getStorageSync('user');
		 const shopCode=uni.getStorageSync('shopCode')
		 if(token&&!shopCode){
			uni.navigateTo({
				url: '../selectArea/selectArea'
			});
		 }
		 if(token&&shopCode){
			uni.navigateTo({
				url: '../main/main'
			}); 
		 }
	}
}
